.about{
    display: flex;
    align-items: center;
    margin-top: 50px;
    justify-content: center;
    flex-direction: column;
}

.title{
    display: flex;
    align-items: center;
}

.title img{
    height: 9.7vw;
}

.aboutTitle{
    font-size: 6.5vw;
    padding-bottom: 4px;
    position: relative;
    text-decoration: none;
}

.aboutTitle::after {
    /* background: #ff0000; */
    background: linear-gradient(to right, transparent 0%, #ff0000 50%, transparent 100%);  
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    height: 2px;
    width: 100%;
 }

.contactTitle img{
    height: 80px;
    z-index: -1;
}

.aboutme{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.aboutme p{
    padding: 0px 30px;
    justify-content: center;
    align-items: center;
}