.skillsSet{
    display: flex;
    align-items: center;
    margin-top: 50px;
    justify-content: center;
    flex-direction: column;
    padding: 20px;
    margin: 20px;
}

.title{
    display: flex;
    align-items: center;
}

.title img{
    height: 7.81vw;
}

.skillTitle{
    font-size: 6.5vw;
    padding-bottom: 4px;
    position: relative;
    text-decoration: none;
}

.skillTitle::after {
    /* background: #ff0000; */
    background: linear-gradient(to right, transparent 0%, #ff0000 50%, transparent 100%);  
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    height: 2px;
    width: 100%;
 }
.skillDisplay{
    display: flex;
    align-content: center;
    flex-wrap: wrap;
}

.skills{
    display: flex;
    /* border: 2px solid white; */
    padding: 20px;
    /* margin: 20px; */
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.skills img{
    margin-top: 10px;
    width: 6.5vw;
}
