.educationModule{
        display: flex;
        align-items: center;
        margin-top: 50px;
        justify-content: center;
        flex-direction: column;
        color: wheat;
}

.title{
    display: flex;
    align-items: center;
}

.title img{
    height: 8.46vw;
}

.educationTitle{
    font-size: 6.5vw;
    padding-bottom: 4px;
    position: relative;
    text-decoration: none;
}

.educationTitle::after {
    /* background: #ff0000; */
    background: linear-gradient(to right, transparent 0%, #ff0000 50%, transparent 100%);  
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    height: 2px;
    width: 100%;
 }

.educationBoxes{
    position: relative;
    width: 40%;
    /* border: 2px solid white; */
}

.educationBox{
    display: flex;
    justify-content: space-between;
    /* border: 2px solid white; */
    margin-bottom: 50px;
}

.instituteLogo img{
    width: 100px;
}

.instututeInfo{
    justify-content: start;
    height: 100px;
    /* border: 2px solid white; */
    width: 80%;  
}

.instituteName{
    font-size: 1.95vw;
    font-weight: bold;
    background: linear-gradient(264deg, #4040bf -5.09%, #ff0000 106.28%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-style: italic;
}

.degree{
    font-size: 1.5vw;
    font-weight: bold;
}

.instituteLocation{
    font-size: 1.3vw;
}

.completedYear{
    font-size: 1vw
}