.navBar{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 20px 170px;
}

.navList{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 60px;
    font-size: 15px;
    padding: 0px;
}

.title{
    font-size: 30px;
    color: #fff;
    font-weight: 600;
    font-family: 'Times New Roman', Times, serif;
    margin-left: 15px;
    &:hover{color: #0f0}
}

.resumeButton{
    padding: 10px 40px;
    border-radius: 50px;
    background: linear-gradient(264deg, #4040bf -5.09%, #ff0000 106.28%);
    cursor: pointer;
    transition: 0.5s;
}

.resumeButton:hover{
    transform: scale(1.05);
}

.mobOpen{
    display: none;
}

.mobClose{
    display: none;
}

@media(max-width: 768px){
    .navBar{
        margin: 20px 50px;
    }
    .resumeButton{
        display: none;
    }
    .mobOpen{
        display: block;
        position: relative;
        /* left: 0px; */
        width: 30px;
    }
    .mobClose{
        display: block;
        position: relative;
        top:10px;
        /* left: 390px; */
        /* right: 100rem; */
        width: 30px; 
        align-self: flex-end;
        padding-right: 20px;
    }

    .navList{
        position: fixed;
        flex-direction: column;
        align-items: start;
        justify-content: start;
        top: 0;
        gap: 30px;
        background-color: #1F0016;
        width: 40%;
        height: 100%;
        z-index: 2;
        transition: 0.5s;
        right: -1000px;
    }
    .navList li{
        font-size: 20px;
        padding-left: 10px;
        flex-direction: row;
        gap: 20px
    }
   
}